import { Component, Input, OnInit } from '@angular/core';
import { PaymentMethod } from 'src/app/api/models';
import { SystemService } from 'src/app/services/system.service';
import { SSUuid } from 'src/app/static-stack/uuid';

@Component({
  selector: 'app-set-payment-method',
  templateUrl: './set-payment-method.component.html',
  styleUrls: ['./set-payment-method.component.scss']
})
export class SetPaymentMethodComponent implements OnInit {

  @Input() settingsData: any;
  settingsDataPropertiesNames: string[] = [];
  selected = "Payment Methods";

  public gwSettingsData: any = {};

  constructor(
    public systemSvc: SystemService
  ) {

  }

  ngOnInit(): void {
    this.settingsDataPropertiesNames = Object.getOwnPropertyNames(this.settingsData);
    this.loadGwSettingsData();
  }

  loadGwSettingsData() {
    if (!this.settingsData[this.selected]) this.settingsData[this.selected] = { "Methods": [] };
    if (!this.settingsData[this.selected]["Methods"]) this.settingsData[this.selected]["Methods"] = [];

    for (let i = 0; i < this.settingsData[this.selected]["Methods"].length; i++) {
      const _method: PaymentMethod = <any>this.settingsData[this.selected]["Methods"][i];
      if (_method.gatewaySettings && _method.gatewaySettings.indexOf("{") >= 0) {
        try {
          this.gwSettingsData[_method.id]=JSON.parse(_method.gatewaySettings);
        } catch (error) {
          console.log("Error parsing gatewaysettings!")
        }
      }
    }
  }

  remove(method: PaymentMethod) {
    this.settingsData[this.selected]["Methods"].splice(
      this.settingsData[this.selected]["Methods"].indexOf(method), 1
    );
  }

  updateGwSettingsData(_method:PaymentMethod){
    var _self=this;
    var _m=_method;
    try {
      _method.gatewaySettings=JSON.stringify(_self.gwSettingsData[_m.id])
    } catch (error) {
      console.log("Error serializing gatewaysettings!")
    }
  }

  add() {
    let newPaymentMethod = <PaymentMethod>{
      id: SSUuid.GenerateV4(),
      createdAt: (new Date()).toISOString(),
      status: "active",
      currency: this.systemSvc.baseCurrency,
      type: "ONLINE",
      method: "DIGITAL_WALLET",
      cardBrand: "ONCHAIN/LIGHTNING",
      displayName: "BITCOIN",
      gateway: "BTCPAY",
      gatewaySettings: "{}",
      merchantId: this.systemSvc.selectedMerchant.id
    };

    this.settingsData[this.selected]["Methods"].push(newPaymentMethod);
    this.loadGwSettingsData();
  }

}
